@-webkit-keyframes slideInUp {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideInUp {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
@-webkit-keyframes slideInDown {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
}
@keyframes slideInDown {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
}
@-webkit-keyframes imgRotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  75% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes imgRotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  75% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.home_header {
  position: absolute;
  top: 0;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0d163f;
}
.home_header_logo {
  width: 36px;
  height: 36px;
  margin: 5px 8px 8px 0;
}
.header_text {
  font-weight: bold;
  color: #f7f1db;
  font-size: 22px;
}
.nav {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-around;
  line-height: 32px;
  background-color: #0d163f;
  top: 45px;
}
.nav_item {
  width: 100%;
  text-align: center;
}
.nav_item:hover.nav_item {
  border-bottom: 3px #f7f1db solid;
}
.nav_item_link {
  display: inline-block;
  width: 100%;
  font-weight: bold;
  font-size: 16px;
  color: #f7f1db;
}
.select {
  border-bottom: 3px #f7f1db solid;
}
.message_container {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -75px;
  margin-top: -75px;
  z-index: 9999;
}
.message_info_container {
  width: 150px;
  height: 150px;
  background: rgba(0,0,0,0.8);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 25%;
  color: #fff;
}
.message_info_container img {
  width: 80px;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 0;
  -webkit-transition: opacity 10s;
  transition: opacity 10s;
}
.fade-enter-done {
  opacity: 1;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 10s;
  transition: opacity 10s;
}
.fade-exit-done {
  opacity: 0;
}
.red {
  background: #f00;
}
.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -40px;
  margin-top: -40px;
}
.loading img {
  width: 80px;
}
.large_player_container {
  z-index: 5;
  background: #0d163f;
  background-size: 100%;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  touch-action: none;
}
.player_display {
  display: none;
}
.large_player_container_progress_bar {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0d163f;
  height: 10vh;
}
.large_player_container_progress_bar>div {
  margin: 0 10px;
}
.progress_bar {
  position: relative;
  background: #a29e92;
  width: 70%;
  height: 2px;
}
.progress_bar_point {
  position: absolute;
  width: 8px;
  height: 8px;
  border: 3px solid #fff;
  border-radius: 50%;
  top: -5px;
  background: #16235b;
  margin-left: -5px;
}
.progress_bar_time {
  color: #f7f1db;
}
.progress_baring {
  width: 0;
  height: 100%;
  background: #f7f1db;
}
.player_lyric {
  color: #f7f1db;
}
.player_lyric_color {
  color: #a29e92;
}
.large_player_container {
  height: 100vh;
}
.large_player_container_header {
  background: #16235b;
  height: 7vh;
  display: flex;
  align-items: center;
}
.large_player_songName {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
  width: 100%;
  font-size: 22px;
  color: #f7f1db;
  padding-right: 30px;
}
.large_player_container_img {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  height: 50vh;
}
.large_player_container_img img {
  border-radius: 50%;
}
.large_player_container_lyric {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 15vh;
  line-height: 24px;
}
.large_player_container_tools {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: #16235b;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 13vh;
}
.player_select {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 40px;
}
.player_select img {
  margin: 0 8px;
}
.player_display {
  display: none;
}
.img_width1_5 {
  width: 24px;
}
.img_width2 {
  width: 30px;
}
.img_width2_5 {
  width: 36px;
}
.img_width3 {
  width: 42px;
}
.img_width4 {
  width: 56px;
}
.small_player_container {
  height: 10vh;
  background: #0d163f;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  touch-action: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.small_player_tools {
  margin-left: -24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.small_player_tools img {
  margin: 0 4px 0 4px;
}
.small_player_img {
  display: flex;
  align-items: center;
  padding: 0;
}
.player_display {
  display: none;
}
.img_width1_5 {
  width: 24px;
}
.img_width2 {
  width: 30px;
}
.img_width2_5 {
  width: 36px;
}
.img_width3 {
  width: 42px;
}
.img_width4 {
  width: 56px;
}
.play_list_display {
  display: none;
}
.play_list {
  bottom: 0;
  right: 0;
  position: absolute;
  background: #16235b;
  width: 100%;
  height: 50vh;
  z-index: 5;
  color: #f7f1db;
  border-radius: 10px 10px 0 0;
  overflow: auto;
}
.play_list_title {
  text-align: center;
  margin-top: 16px;
}
.play_list_close {
  padding: 8px;
  position: absolute;
  right: 8px;
  top: 8px;
}
.play_list_total {
  padding: 8px 0;
  text-indent: 32px;
  border-bottom: 1px solid #0d163f;
}
.play_list_item {
  padding: 13px 0;
  border-bottom: 1px solid #0d163f;
  display: flex;
  align-items: center;
}
.play_list_song_name {
  margin-left: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.play_list_item>span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.play_list_item span:nth-child(1) {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 40%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.play_list_item span:nth-child(2) {
  display: inline-block;
  width: 25%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.play_list_item span:nth-child(3) {
  display: inline-block;
  width: 15%;
  text-align: center;
}
.play_list_item span:nth-child(4) {
  display: inline-block;
  width: 20%;
}
.play_list_add,
.play_list_delete {
  width: 24px;
}
.play_list_delete {
  margin-left: 12px;
}
.play_list_triangle {
  width: 16px;
  margin: -3px 0 0 16px;
}
.scroll_container {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  overflow: hidden;
}
.scroll {
  position: absolute;
  touch-action: none;
  width: 100%;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 0;
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.fade-enter-done {
  opacity: 1;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.fade-exit-done {
  opacity: 0;
}
